<template>
  <div class="container" style="margin-top:7%; height:100%">
    <!-- Outer Row -->
    <div class="row justify-content-center">
      <div class="col-xl-10 col-lg-12 col-md-9">
        <div class="card o-hidden border-0 shadow-lg my-5">
          <div class="card-body p-0">
            <!-- Nested Row within Card Body -->
            <div class="row">
              <div class="col-lg-6 d-none d-lg-block bg-login-image"></div>
              <div class="col-lg-6">
                <div class="p-5">
                  <div class="text-center">
                    <h1 class="h4 text-gray-900 mb-4">Welcome Back!</h1>
                  </div>
                  <form @submit.prevent="submit()" class="user">
                    <div class="form-group">
                      <input
                        type="email"
                        class="form-control form-control-user"
                        id="exampleInputEmail"
                        v-model="email"
                        aria-describedby="emailHelp"
                        placeholder="Enter Email Address..."
                        required
                      />
                    </div>
                    <div class="form-group">
                      <input
                        type="password"
                        class="form-control form-control-user"
                        id="exampleInputPassword"
                        placeholder="Password"
                        v-model="password"
                        required
                      />
                    </div>
                    <!-- <div class="form-group">
                      <div class="custom-control custom-checkbox small">
                        <input type="checkbox" class="custom-control-input" id="customCheck" />
                        <label class="custom-control-label" for="customCheck">Remember Me</label>
                      </div>
                    </div> -->
                    <button type="submit" class="btn btn-primary btn-user btn-block">Login</button>
                    <hr />
                    <!-- <a href="index.html" class="btn btn-google btn-user btn-block">
                      <i class="fab fa-google fa-fw"></i> Login with Google
                    </a>
                    <a href="index.html" class="btn btn-facebook btn-user btn-block">
                      <i class="fab fa-facebook-f fa-fw"></i> Login with Facebook
                    </a> -->
                  </form>
                  
                  <!-- <div class="text-center">
                    <a class="small" href="forgot-password.html">Forgot Password?</a>
                  </div>
                  <div class="text-center">
                    <a class="small" href="register.html">Create an Account!</a>
                  </div> -->
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
var $ = global.jQuery;
window.$ = $;
import axios from 'axios';
export default {
  data() {
    return {
      user: "",
      fullPage: true,
      email: "",
      password: "",
      loader: null,
    };
  },
  methods: {
    toggleMenu() {
      $("body").toggleClass("sidebar-toggled");
      $(".sidebar").toggleClass("toggled");
    },
    submit() {
      
      this.displayLoader();
      if (this.email == "" || this.password == "") {
        this.$swal({
          icon: "error",
          title: "Oops...",
          text: "All fields are required!"
        });
        this.loader.hide();
        return;
      }
     // this.loader.hide();
     console.log("Api route "+ process.env.VUE_APP_ROOT_API);
      axios
        .post(process.env.VUE_APP_ROOT_API+ "adminSignIn", {
          email: this.email,
          password: this.password
        })
        .then(response => {
          //  console.log(response);
          const token = response.data.token;
          const admin = response.data.user;
          localStorage.setItem("admintoken", token);
          localStorage.setItem("admin", JSON.stringify(admin));
          this.$swal({
            icon: "success",
            title: "Login Successful",
            text: 'Welcome'
          });
          if (token != null) {
            console.log(response.data.user);
            this.loader.hide();
            this.$router.push({ path: '/' }); 
          }
        })
        .catch(error => {
          const validationerror = JSON.parse(
            JSON.stringify(error.response.data)
          );
          console.log(validationerror);
          this.$swal({
          icon: "error",
          title: "Oops...",
          text: validationerror.error
        });

        this.loader.hide();
       
        });
    },
    onCancel() {
      console.log("User cancelled the loader.");
    },
    displayLoader(){
        this.loader = this.$loading.show({
        // Optional parameters
        container: this.fullPage ? null : this.$refs.formContainer,
        canCancel: false,
        onCancel: this.onCancel,
        color: "#4e73df",
        loader: "bars",
        width: 100,
        height: 100,
        backgroundColor: "#e1e1e1",
        opacity: 0.6,
        zIndex: 999
      });
    }
  },
  mounted() {
    //this.user = JSON.parse(localStorage.getItem('admin'));
    // console.log(user);
  }
};
</script>
