import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import VueSweetalert2 from 'vue-sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';
import VModal from 'vue-js-modal';
/* Admin Components */
import HomePage from './components/HomePage.vue';
import AdminLogin from './components/AdminLogin.vue';
import AdminAddProduct from './components/AddProduct.vue';
import AdminAddCategory from './components/AddCategory.vue';
import AdminViewProducts from './components/ViewProducts.vue';
import AdminViewCategories from './components/ViewCategories.vue';
import ViewUsers from './components/Users.vue';
import PendingOrder from './components/PendingOrder.vue';
import CompletedOrder from './components/CompletedOrders.vue';
import UnPaidOrders from './components/UnPaidOrders.vue';
import AddShipping from './components/AddShipping.vue';
import ManageShipping from './components/ManageShipping.vue';
import ManageShippingStates from './components/ManageShippingStates.vue';


Vue.use(VueSweetalert2);
Vue.use(Loading);
Vue.use(VModal);

Vue.config.productionTip = false
Vue.use(VueRouter);

var $ = global.jQuery;
window.$ = $;
function guardMyroute(to, from, next) {
    //$("body").toggleClass("sidebar-toggled");
    $(".sidebar").toggleClass("toggled");
    const token = localStorage.getItem("admintoken");
    if(token == 'null'){
      next('/login');
    }else{
      next();
    } // allow to enter route
}

const routes = [
  { name: 'login', path: '/login', component: AdminLogin },
  { name: 'home', path: '/', beforeEnter : guardMyroute, component: HomePage },
  { name: 'addCategory', path: '/addCategory', beforeEnter : guardMyroute, component: AdminAddCategory },
  { name: 'addProduct', path: '/addProduct', beforeEnter : guardMyroute, component: AdminAddProduct },
  { name: 'viewProducts', path: '/viewProducts', beforeEnter : guardMyroute, component: AdminViewProducts },
  { name: 'viewCategories', path: '/viewCategories', beforeEnter : guardMyroute, component: AdminViewCategories },
  { name: 'viewUsers', path: '/viewUsers', beforeEnter : guardMyroute, component: ViewUsers },
  { name: 'pendingOrders', path: '/pendingOrders', beforeEnter : guardMyroute, component: PendingOrder },
  { name: 'completedOrders', path: '/completedOrders', beforeEnter : guardMyroute, component: CompletedOrder },
  { name: 'unPaidOrders', path: '/unPaidOrders', beforeEnter : guardMyroute, component: UnPaidOrders },
  { name: 'addShipping', path: '/addShipping', beforeEnter : guardMyroute, component: AddShipping },
  { name: 'manageShipping', path: '/manageShipping', beforeEnter : guardMyroute, component: ManageShipping },
  { name: 'manageShippingStates', path: '/manageShippingStates', beforeEnter : guardMyroute, 
           component: ManageShippingStates, 
           props: { default: true, country_id: 0 } },
];

const router = new VueRouter({
  mode: 'history',
  routes: routes,
});

