//   let check = localStorage.getItem("coming_soon");
//   if (check == null) {
//     window.location = "https://smartshoppersplaze.com/comingsoon/";
//   }
//   next();
// });

new Vue({
  el: '#app',
  router: router,
  render: h => h(App),
})
