</<template>
    <div class="container-fluid">
        <!-- Content Row -->
    <div class="row">
            <!-- Area Chart -->
    
            <div class="col-xl-6 col-lg-6">
    
                <div class="card shadow mb-4">
    
                    <!-- Card Header - Dropdown -->
    
                    <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
    
                        <h6 class="m-0 font-weight-bold text-primary">Add Country Price</h6>
    
                        <div class="dropdown no-arrow">
    
                            <a class="dropdown-toggle" href="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
    
                          <i class="fas fa-ellipsis-v fa-sm fa-fw text-gray-400"></i>
    
                        </a>
    
                            <div class="dropdown-menu dropdown-menu-right shadow animated--fade-in" aria-labelledby="dropdownMenuLink">
    
                                <div class="dropdown-header">Dropdown Header:</div>
    
                                <a class="dropdown-item" href="#">Action</a>
    
                                <a class="dropdown-item" href="#">Another action</a>
    
                                <div class="dropdown-divider"></div>
    
                                <a class="dropdown-item" href="#">Something else here</a>
    
                            </div>
    
                        </div>
    
                    </div>
    
                    <!-- Card Body -->
    
                    <div class="card-body">
    
                        <div>
    
                            <form @submit.prevent="submitCountry" class="form-group">

                                <div class="form-group">
                                    <label for="select-type">Country Name</label>
                                    <input v-model.trim="country_details.country_name" placeholder="Enter Country Name." class="form-control" type="text" required /> 
                                </div>

                                 <div class="form-group">
                                    <label for="select-type">Country Price</label>
                                    <input v-model.trim="country_details.country_price" oninput="validity.valid||(value='');" min="1" placeholder="Add Price." class="form-control" type="number" required /> 
                                </div>

                                <div class="form-group">
                                    <button type="submit" class="form-control btn btn-primary">
                                         Save Details
                                    </button>
                                </div>

                            </form>
    
                        </div>
    
                    </div>
    
                </div>
               
    
            
            </div>
            <div class="col-xl-6 col-lg-6">
             <div class="card shadow mb-4">
    
                    <!-- Card Header - Dropdown -->
    
                    <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
    
                        <h6 class="m-0 font-weight-bold text-primary">Add State Price</h6>
    
                        <div class="dropdown no-arrow">
    
                            <a class="dropdown-toggle" href="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
    
                          <i class="fas fa-ellipsis-v fa-sm fa-fw text-gray-400"></i>
    
                        </a>
    
                            <div class="dropdown-menu dropdown-menu-right shadow animated--fade-in" aria-labelledby="dropdownMenuLink">
    
                                <div class="dropdown-header">Dropdown Header:</div>
    
                                <a class="dropdown-item" href="#">Action</a>
    
                                <a class="dropdown-item" href="#">Another action</a>
    
                                <div class="dropdown-divider"></div>
    
                                <a class="dropdown-item" href="#">Something else here</a>
    
                            </div>
    
                        </div>
    
                    </div>
    
                    <!-- Card Body -->
    
                    <div class="card-body">
    
                        <div>
    
                            <form @submit.prevent="submitState" class="form-group">

                                <div class="form-group">
                                      <select class="form-control col-12" v-model="state_details.country_id" required>
                                        <option value="" selected disabled>Select Country</option>                  
                                        <option v-for="(info,$index) in allCountries" :key="$index" :value="info.id">{{ info.country_name }}</option>
                                      </select>
                                </div>
                                
                                <div class="form-group">
                                    <label for="select-type">State Name</label>
                                    <input v-model.trim="state_details.state_name" placeholder="State Name." class="form-control" type="text" required /> 
                                </div>

                                <div class="form-group">
                                    <label for="select-type">State Price</label>
                                    <input v-model.trim="state_details.state_price" oninput="validity.valid||(value='');" min="1" placeholder="Add Price." class="form-control" type="number" required /> 
                                </div>

                                <div class="form-group">
                                    <button type="submit" class="form-control btn btn-primary">
                                         Save Details
                                    </button>
                                </div>

                            </form>
    
                        </div>
    
                    </div>
    
                </div>
    
            </div>
    
            <!-- Pie Chart -->
    
    
        </div>
    </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      token: localStorage.getItem("admintoken"),
      user: "",
      loader: null,
      allCountries: [],
      country_details: {
            country_name : "",
            country_price : ""
      },
      state_details: {
            country_id : "",
            state_name: "",
            state_price : ""
      },
      
    };
  },
  methods: {
    submitState(e) {
      this.displayLoader();
      axios
        .post(
          process.env.VUE_APP_ROOT_API +
            "admin/addShippingState?token=" +
            this.token,
            this.state_details
        )
        .then(() => {
          //console.log(response.data);
          e.target.reset();
          this.$swal({
            icon: "success",
            title: "Uploaded Successfully",
            text: "State is now available."
          });

          this.allCountries.push(this.country_details.country_name);          
          this.loader.hide();
        })
        .catch(error => {
          const validationerror = JSON.parse(
            JSON.stringify(error.response.data)
          );
          this.loader.hide();
          console.log("Error - ",validationerror);
          if (error.response.status == 422) {
            
            const state_name_error = validationerror.errors.state_name;

            if (state_name_error != null) {
              this.$swal({
                icon: "error",
                title: "Fail to upload",
                text: state_name_error
              });
            }
          }

          if (validationerror.response.error != null) {

              this.$swal({
                icon: "error",
                title: "Fail to add state",
                text: validationerror.response.error
              });
            
          }
        });
    },
    submitCountry(e) {
      this.displayLoader();
      axios
        .post(
          process.env.VUE_APP_ROOT_API +
            "admin/addShippingCountry?token=" +
            this.token,
            this.country_details
        )
        .then((res) => {
          //console.log(res.data.response.response);
          e.target.reset();
          this.$swal({
            icon: "success",
            title: "Uploaded Successfully",
            text: "Country is now available."
          });

          this.allCountries.push(res.data.response.response);          
          this.loader.hide();
        })
        .catch(error => {
          const validationerror = JSON.parse(
            JSON.stringify(error.response.data)
          );
          this.loader.hide();
          //console.log("Error - ",validationerror);
          if (error.response.status == 422) {
            
            const country_name_error = validationerror.errors.country_name;

            if (country_name_error != null) {
              this.$swal({
                icon: "error",
                title: "Fail to upload",
                text: country_name_error
              });
            }
          }

          // if (validationerror.errors.country_name != null) {
          //   //console.log(validationerror);
           
          //     this.$swal({
          //       icon: "error",
          //       title: "Fail to upload",
          //       text: validationerror.errors.country_name
          //     });
            
          // }
        });
    },
    displayLoader() {
      this.loader = this.$loading.show({
        // Optional parameters
        container: null,
        canCancel: false,
        onCancel: this.onCancel,
        color: "#4e73df",
        loader: "bars",
        width: 100,
        height: 100,
        backgroundColor: "#e1e1e1",
        opacity: 0.6,
        zIndex: 999
      });
    },
    fetchAllCountries() {
      this.displayLoader();
      axios
        .get(
          process.env.VUE_APP_ROOT_API +
            "fetchCountries"
        )
        .then(response => {
          const myresponse = JSON.parse(JSON.stringify(response.data.response));
          this.allCountries = myresponse;
          this.loader.hide();
        })
        .catch(() => {
          this.loader.hide();
          this.$swal({
            icon: "error",
            title: "Session TimeOut",
            text: ""
          });
        });
    },
   
  },
  mounted() {
    this.fetchAllCountries();
  }
};
</script>