<template>
  <div class="container-fluid">
    <!-- Content Row -->

    <div class="row">
      <!-- Pie Chart -->

      <div class="col-xl-12 col-lg-12">
        <div class="card shadow mb-4">
          <!-- Card Header - Dropdown -->

          <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
            <h6 class="m-0 font-weight-bold text-danger">All Unpaid Orders</h6>

            <div class="dropdown no-arrow">
              <a
                class="dropdown-toggle"
                href="#"
                role="button"
                id="dropdownMenuLink"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                <i class="fas fa-ellipsis-v fa-sm fa-fw text-gray-400"></i>
              </a>

              <div
                class="dropdown-menu dropdown-menu-right shadow animated--fade-in"
                aria-labelledby="dropdownMenuLink"
              >
                <div class="dropdown-header">Dropdown Header:</div>

                <a class="dropdown-item" href="#">Action</a>

                <a class="dropdown-item" href="#">Another action</a>

                <div class="dropdown-divider"></div>

                <a class="dropdown-item" href="#">Something else here</a>
              </div>
            </div>
          </div>

          <!-- Card Body -->

          <div class="card-body">
            <table class="table table-striped table-responsive-md btn-table">
              <thead>
                <tr>
                  <th>id</th>
                  <th>Email</th>
                  <th>Phone</th>
                  <th>Amount</th>
                  <th>Location</th>
                  <th class="text-danger">Status</th>
                  <th>Registration Date</th>
                  <th>Action</th>
                </tr>
              </thead>

              <tbody>
                <tr v-for="(info,$index) in allOrders" :key="$index" :value="info.id">
                  <th scope="row">{{ info.id }}</th>
                  <td>{{ info.buyerEmail }}</td>
                  <td>{{ info.shippingAddress_phone }}</td>
                  <td>{{ info.subTotal }}</td>
                  <td
                    style="white-space: pre;"
                  >{{ info.shipToAddress_Country+"\n"+info.shipToAddress_State+"\n"+info.shipToAddress_City }}</td>
                  <td class="text-danger">
                    {{ info.status == 0 ? "No Payment" :
                    info.status == 1 ? "Paid" :
                    info.status == 2 ? "Processing" : 
                    info.status == 3 ? "Completed" : "Unknown Status" 
                    }}
                  </td>
                  <td>{{ info.created_at }}</td>
                  <td>
                    <button class="btn btn-sm btn-primary" @click="showModal($index,info)">
                      <i class="fa fa-eye"></i>
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
            <a href>View All...</a>
          </div>
        </div>
      </div>
    </div>
    <modal
      v-if="modalData != null"
      name="ViewModal"
      :adaptive="true"
      :scrollable="true"
      height="auto"
      style="over-flow:auto !important"
    >
      <div class="card">
        <div slot="top-right">
          <button class="btn" @click="closeModal"></button>
        </div>

        <div class="card-body">
          <div class="card-title">
            <h3>{{ modalData.paymentReference }}</h3>
          </div>
          <div class="row">
            <div class="col-3">Status</div>
            <div class="col-9 text-danger">No Payment</div>
          </div>

          <div class="row">
            <div class="col-3">Email</div>
            <div class="col-9">{{ modalData.buyerEmail }}</div>
          </div>
          <div class="row">
            <div class="col-3 text-danger">No Payment</div>
            <div class="col-9 text-danger">{{ modalData.subTotal }}</div>
          </div>
          <hr />
          <h3>Shipping Details</h3>
          <hr />
          <div class="row">
            <div class="col-3">Firstname</div>
            <div class="col-9">{{ modalData.shipToAddress_Firstname }}</div>
          </div>

          <div class="row">
            <div class="col-3">Lastname</div>
            <div class="col-9">{{ modalData.shipToAddress_Lastname }}</div>
          </div>

          <div class="row">
            <div class="col-3">Country</div>
            <div class="col-9">{{ modalData.shipToAddress_Country }}</div>
          </div>

          <div class="row">
            <div class="col-3">State</div>
            <div class="col-9">{{ modalData.shipToAddress_State }}</div>
          </div>

          <div class="row">
            <div class="col-3">City</div>
            <div class="col-9">{{ modalData.shipToAddress_City }}</div>
          </div>

          <div class="row">
            <div class="col-3">Street</div>
            <div class="col-9">{{ modalData.shipToAddress_Street }}</div>
          </div>

          <div class="row">
            <div class="col-3">Postal Code</div>
            <div class="col-9">{{ modalData.shipToAddress_Postalcode }}</div>
          </div>

          <div class="row">
            <div class="col-3">Full Address</div>
            <div class="col-9">{{ modalData.shippingFullAddress }}</div>
          </div>

          <div class="row">
            <div class="col-3">Phone</div>
            <div class="col-9">{{ modalData.shipToAddress_phone }}</div>
          </div>

          <div class="row">
            <div class="col-3">Reg Date</div>
            <div class="col-9">{{ modalData.created_at }}</div>
          </div>

          <hr />
          <h3>Product Details</h3>
          <hr />

          <div class="col-12">
            <table class="table table-responsive">
              <thead>
                <th>Picture</th>
                <th>Product name</th>
                <th>Quantity</th>
              </thead>

              <tbody>
                <tr v-for="(info,$index) in modalData.order_items" :key="$index">
                  <td>
                    <img />
                  </td>
                  <td>{{ info.product_name }}</td>
                  <td>{{ info.requested_quantity }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      token: localStorage.getItem("admintoken"),
      allOrders: [],
      modalData: {},
    };
  },
  methods: {
    showModal(index,info) {
      this.modalData = info;
      this.modalData.index = index;
      this.$modal.show("ViewModal");
    },
    closeModal() {
      this.$modal.hide("ViewModal");
    },

    fetchUnPaidOrders() {
      this.displayLoader();
      axios
        .post(
          process.env.VUE_APP_ROOT_API +
            "admin/fetchByStatusOrders?token=" +
            this.token,{
                status: 'unpaid'
            }
        )
        .then((response) => {
          const myresponse = JSON.parse(JSON.stringify(response.data.response));
          this.allOrders = myresponse;
          //console.log("Report - ", myresponse[0].status);
          this.loader.hide();
        })
        .catch(() => {
          this.loader.hide();
          this.$swal({
            icon: "error",
            title: "Session TimeOut",
            text: "",
          });
        });
    },
    displayLoader() {
      this.loader = this.$loading.show({
        // Optional parameters
        container: null,
        canCancel: false,
        onCancel: this.onCancel,
        color: "#4e73df",
        loader: "bars",
        width: 100,
        height: 100,
        backgroundColor: "#e1e1e1",
        opacity: 0.6,
        zIndex: 999,
      });
    },
  },
  mounted() {
    // this.fetchAllCategories();
    this.fetchUnPaidOrders();
  },
};
</script>

<style>
.vm--modal {
  overflow: auto !important;
}
</style>