</<template>
    <div class="container-fluid">
    
    
    
    
    
    
    
        <!-- Content Row -->
    
    
    
        <div class="row">
    
    
    
            <!-- Area Chart -->
    
            <div class="col-xl-6 col-lg-6">
    
                <div class="card shadow mb-4">
    
                    <!-- Card Header - Dropdown -->
    
                    <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
    
                        <h6 class="m-0 font-weight-bold text-primary">Add Category</h6>
    
                        <div class="dropdown no-arrow">
    
                            <a class="dropdown-toggle" href="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
    
                          <i class="fas fa-ellipsis-v fa-sm fa-fw text-gray-400"></i>
    
                        </a>
    
                            <div class="dropdown-menu dropdown-menu-right shadow animated--fade-in" aria-labelledby="dropdownMenuLink">
    
                                <div class="dropdown-header">Dropdown Header:</div>
    
                                <a class="dropdown-item" href="#">Action</a>
    
                                <a class="dropdown-item" href="#">Another action</a>
    
                                <div class="dropdown-divider"></div>
    
                                <a class="dropdown-item" href="#">Something else here</a>
    
                            </div>
    
                        </div>
    
                    </div>
    
                    <!-- Card Body -->
    
                    <div class="card-body">
    
                        <div>
    
                            <form @submit.prevent="submitCategory" class="form-group">
    
                                <div class="form-group">
                                    <label for="select-type">Category Name</label>
                                    <input v-model="category_name" placeholder="Category Name." class="form-control" type="text" required /> 
                                </div>

                                <div class="form-group">
                                    <label for="category_details">Category Details</label>
                                    <textarea v-model="category_details" placeholder="Category Details" class="form-control" type="text" required>
                                    </textarea>
                                </div>

                                <div class="custom-file form-group" style="margin-bottom: 5px;">
                                    <label for="category_image" style="overflow: hidden;" class="custom-file-label">{{ filename }}</label>
                                    <input @change="onImageChange"  class="custom-file-input" type="file" required /> 
                                </div>

                                <div class="form-group">
                                    <button type="submit" class="form-control btn btn-primary">
                                         Save Category
                                    </button>
                                </div>

                            </form>
    
                        </div>
    
                    </div>
    
                </div>
    
            </div>
    
    
    
            <!-- Pie Chart -->
    
            <div class="col-xl-6 col-lg-6">
    
                <div class="card shadow mb-4">
    
                    <!-- Card Header - Dropdown -->
    
                    <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
    
                        <h6 class="m-0 font-weight-bold text-primary">All Categories</h6>
    
                        <div class="dropdown no-arrow">
    
                            <a class="dropdown-toggle" href="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
    
                          <i class="fas fa-ellipsis-v fa-sm fa-fw text-gray-400"></i>
    
                        </a>
    
                            <div class="dropdown-menu dropdown-menu-right shadow animated--fade-in" aria-labelledby="dropdownMenuLink">
    
                                <div class="dropdown-header">Dropdown Header:</div>
    
                                <a class="dropdown-item" href="#">Action</a>
    
                                <a class="dropdown-item" href="#">Another action</a>
    
                                <div class="dropdown-divider"></div>
    
                                <a class="dropdown-item" href="#">Something else here</a>
    
                            </div>
    
                        </div>
    
                    </div>
    
                    <!-- Card Body -->
    
                    <div class="card-body d-block scroll-wrapper" style="overflow:auto;height: 450px">
                        <div  v-for="(info,$index) in categories" :key="$index">
                            <h4 class="small font-weight-bold">{{ info.category_name }} <span class="float-right">20</span></h4>
        
                            <div class="progress mb-4">
                                <div :class="['progress-bar', categoryStyles[Math.floor(Math.random() * categoryStyles.length)]]"  role="progressbar" style="width: 20%" aria-valuenow="20" aria-valuemin="0" aria-valuemax="100"></div>
                            </div>
                            </div>
                        <div> 
                            <a href="">View All...</a>
                        </div>  
                    </div>
    
                </div>
    
            </div>
    
        </div>
    </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      token: localStorage.getItem("admintoken"),
      user: "",
      category_name: "",
      category_details: "",
      category_image: "",
      filename: "Category Image",
      loader: null,
      myresponse: {},
      categories:[],
      categoryStyles: ["bg-warning", "bg-info", "bg-success", "bg-danger"]
    };
  },
  filters:{
    checkLength(value){
       if (value.length < 25 ){
          return value;
       }
       var result = value.substring(0,25)+".." ;
       return result;
       
    }
  },
  methods: {
    onImageChange(e) {
      this.category_image = e.target.files[0];
      this.filename = this.category_image.name;
    },

    submitCategory(e) {
      this.displayLoader();
      let formData = new FormData();
      formData.append(
        "category_image",
        this.category_image,
        this.category_image.name
      );
      formData.append("category_name", this.category_name);
      formData.append("category_details", this.category_details);

      axios
        .post(
          process.env.VUE_APP_ROOT_API +
            "admin/addCategory?token=" +
            this.token,
          formData
        )
        .then(() => {
          //console.log(response.data);
          e.target.reset();
          this.$swal({
            icon: "success",
            title: "Uploaded Successfully",
            text: "Category is now available."
          });
          //const myresponse = JSON.parse(JSON.stringify(response.data));
          //console.log("Report - ", myresponse);
          var newCategory = {"category_name" : this.category_name};
          
          this.categories.push(newCategory);
          this.category_name = "";
          this.category_details = "";
          this.category_image = "";
          this.filename = "Category Image";

          this.loader.hide();
        })
        .catch(error => {
          const validationerror = JSON.parse(
            JSON.stringify(error.response.data)
          );
          this.loader.hide();
          if (error.response.status == 422) {
           // console.log(validationerror);
            const category_name_error = validationerror.errors.category_name;

            if (category_name_error != null) {
              this.$swal({
                icon: "error",
                title: "Fail to upload",
                text: category_name_error
              });
            }
          }
        });
    },
    displayLoader() {
      this.loader = this.$loading.show({
        // Optional parameters
        container: null,
        canCancel: false,
        onCancel: this.onCancel,
        color: "#4e73df",
        loader: "bars",
        width: 100,
        height: 100,
        backgroundColor: "#e1e1e1",
        opacity: 0.6,
        zIndex: 999
      });
    },
    fetchAllCategories() {
      this.displayLoader();
      axios
        .get(
          process.env.VUE_APP_ROOT_API +
            "admin/allCategories?token=" +
            this.token
        )
        .then(response => {
          const myresponse = JSON.parse(JSON.stringify(response.data.response));
          this.categories = myresponse;
          console.log("Report - ", myresponse);
          this.loader.hide();
        })
        .catch(() => {
          // const validationerror = JSON.parse(
          //   JSON.stringify(error.response.data)
          // );
          this.loader.hide();
          this.$swal({
            icon: "error",
            title: "Session TimeOut",
            text: ""
          });
        });
    }
  },
  mounted() {
    this.fetchAllCategories();
  }
};
</script>