<template>
  <div class="container-fluid">
    <!-- Content Row -->

    <div class="row">
      <!-- Pie Chart -->

      <div class="col-xl-12 col-lg-12">
        <div class="card shadow mb-4">
          <!-- Card Header - Dropdown -->

          <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
            <h6 class="m-0 font-weight-bold text-primary">All Products</h6>

            <div class="dropdown no-arrow">
              <a
                class="dropdown-toggle"
                href="#"
                role="button"
                id="dropdownMenuLink"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                <i class="fas fa-ellipsis-v fa-sm fa-fw text-gray-400"></i>
              </a>

              <div
                class="dropdown-menu dropdown-menu-right shadow animated--fade-in"
                aria-labelledby="dropdownMenuLink"
              >
                <div class="dropdown-header">Dropdown Header:</div>

                <a class="dropdown-item" href="#">Action</a>

                <a class="dropdown-item" href="#">Another action</a>

                <div class="dropdown-divider"></div>

                <a class="dropdown-item" href="#">Something else here</a>
              </div>
            </div>
          </div>

          <!-- Card Body -->

          <div class="card-body">
            <table class="table table-striped table-responsive-md btn-table">
              <thead>
                <tr>
                  <th>#</th>
                  <th>Picture</th>
                  <th>Name</th>
                  <th>Price</th>
                  <th>Upload Date</th>
                  <th>Action</th>
                </tr>
              </thead>

              <tbody>
                <tr v-for="(info,$index) in allproducts" :key="$index" :value="info.id">
                  <th scope="row">{{ $index + 1 }}</th>
                  <td>
                    <img
                      :src="getImage(info.productimages[0].image_location)"
                      style="max-height:50px;"
                      class="img-fluid"
                    />
                  </td>
                  <td>{{ info.product_name }}</td>
                  <td>{{ info.product_price }}</td>
                  <td>{{ info.created_at }}</td>
                  <td>
                    <button class="btn btn-danger" @click="deleteProduct(info.id,$index)">Delete</button>
                    <button class="btn btn-primary" @click="showModal(info)">Edit</button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <modal
      v-if="modalData != null"
      name="EditModal"
      :adaptive="true"
      :scrollable="true"
      height="auto"
      style="over-flow:auto !important"
    >
      <div class="card">
        <div slot="top-right">
          <button class="btn" @click="closeModal"></button>
        </div>
        <!-- <div class="card-title container">
             <h3>{{ modalData.product_name }}</h3>
        </div>-->
        <div class="card-body">
          <div class="row">
            <div class="col-12 col-xl-6 card-title">
              <img style="margin-bottom: 5px;" class="img-fluid" v-if="editDetails.imgUrl" :src="editDetails.imgUrl" />
              <img v-if="editDetails.imgUrl == ''" :src="getImage(modalData.productimages[0].image_location)" class="img-fluid" />
            </div>
            <!-- <div class="col-12"> -->

            <div class="col-12 col-xl-6" style="margin-bottom:10px;">
              <form @submit.prevent="editProduct">
                <div class="form-group">
                  <select v-model="editDetails.category_id" class="form-control col-12" required>
                    <option    
                      v-for="(info,$index) in categories"
                      :selected="editDetails.category_id == info.categories_id"
                      :key="$index"
                      :value="info.id"
                    >{{ info.category_name }}</option>
                  </select>
                </div>

                <div class="form-group">
                  <label for="select-type">Product Name</label>
                  <input
                    v-model="editDetails.product_name"
                    placeholder="Enter Product Name."
                    class="form-control"
                    type="text"
                    required
                  />
                </div>

                <div class="form-group">
                  <label for="product_details">Product Details</label>
                  <textarea
                    v-model="editDetails.product_details"
                    placeholder="Enter Product Details"
                    class="form-control"
                    type="text"
                    required
                  ></textarea>
                </div>

                <div class="form-group">
                  <label for="select-type">Product Price</label>
                  <input
                    v-model="editDetails.product_price"
                    placeholder="Enter Product Price."
                    class="form-control"
                    type="number"
                    required
                  />
                </div>

                <div class="form-group">
                  <label for="select-type">In Stock</label>
                  <input
                    v-model="editDetails.instock"
                    placeholder="How many products are in stock."
                    class="form-control"
                    type="number"
                    required
                  />
                </div>

                <div class="form-group">
                  <label for="select-type">Product Discount (Optional)</label>
                  <input
                    v-model="editDetails.discount"
                    placeholder="Add discount if available"
                    class="form-control"
                    type="number"
                    required
                  />
                </div>

                <div class="custom-file form-group" style="margin-bottom: 5px;">
                  <label for="category_image" style="overflow: hidden;" class="custom-file-label">{{ editDetails.filename }}</label>
                  <input @change="onImageChange" class="custom-file-input" type="file"/>
                </div>

               
                <div class="form-group">
                  <button type="submit" class="form-control btn btn-primary">Edit Product</button>
                </div>
              </form>
            </div>

            <!-- </div> -->
          </div>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      token: localStorage.getItem("admintoken"),
      user: "",
      product_name: "",
      product_details: "",
      product_price: "",
      discount: 0,
      instock: 0,
      product_image: "",
      loader: null,
      imgUrl: null,
      categories: [],
      myresponse: {},
      allproducts: [],
      modalData: null,
      editDetails: {
        product_id : "",
        category_id: "",
        product_name: "",
        product_details: "",
        product_price: "",
        discount: 0,
        instock: 0,
        filename:"",
        product_image: "",
        imgUrl: ""
      }
    };
  },
  methods: {
    onImageChange(e) {
      this.editDetails.product_image = e.target.files[0];
      this.editDetails.filename = this.editDetails.product_image.name;
      this.editDetails.imgUrl = URL.createObjectURL(this.editDetails.product_image);
    },
    editProduct() {
      this.displayLoader();
      let formData = new FormData();
      if(this.editDetails.product_image != ""){
        formData.append(
          "product_image",
          this.editDetails.product_image,
          this.editDetails.product_image.name
        );
      }
      formData.append("product_id", this.editDetails.product_id);
      formData.append("product_name", this.editDetails.product_name);
      formData.append("product_details", this.editDetails.product_details);
      formData.append("product_price", this.editDetails.product_price);
      formData.append("discount", this.editDetails.discount);
      formData.append("in_stock", this.editDetails.instock);
      formData.append("categories_id", this.editDetails.category_id);
      axios
        .post(
          process.env.VUE_APP_ROOT_API +
            "admin/editProduct?token=" +
            this.token,
          formData
        )
        .then((res) => {
          //console.log(response.data);
          //e.target.reset();
          this.$swal({
            icon: "success",
            title: "Edited Successfully",
            text: "Product is now available."
          });

          this.allproducts = res.data.response;
          // const myresponse = JSON.parse(JSON.stringify(res.data.response));
          // console.log("Report - ", myresponse);
          
          // this.imgUrl  = "null";
          // this.filename = "Product Image";
          
          this.loader.hide();
        })
        .catch(error => {
          const validationerror = JSON.parse(
            JSON.stringify(error.response.data)
          );
          this.loader.hide();
          if (error.response.status == 422) {
            //console.log(validationerror);
            const category_name_error = validationerror.errors.category_name;
            const product_name_error = validationerror.errors.product_name;

            if (product_name_error != null) {
              this.$swal({
                icon: "error",
                title: "Use a unique product name",
                text: product_name_error
              });
            }
            if (category_name_error != null) {
              this.$swal({
                icon: "error",
                title: "Fail to Edit",
                text: category_name_error
              });
            }
          }

        });
    },
    showModal(info) {
      this.editDetails = {
        product_id: info.id,
        category_id: info.categories_id,
        product_name: info.product_name,
        product_details: info.product_details,
        product_price: info.product_price,
        discount: info.discount,
        instock: info.in_stock,
        filename:"",
        product_image: "",
        imgUrl: ""
      }
      this.modalData = info;
      this.$modal.show("EditModal");
    },
    closeModal() {
      this.$modal.hide("EditModal");
    },
    deleteProduct(productId, index) {
      //alert(productId);
      this.$swal({
        icon: "warning",
        title: "Are you sure?",
        text: "You won't be able to revert this.",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, Delete it!"
      }).then(result => {
        if (result.value) {
          this.displayLoader();
          axios
            .post(
              process.env.VUE_APP_ROOT_API +
                "admin/deleteProduct?token=" +
                this.token,
              { productId: productId }
            )
            .then(() => {
              //const myresponse = JSON.parse(JSON.stringify(response.data.response));
              // this.categories = myresponse;
              this.$swal({
                icon: "success",
                title: "Product Deleted Successfully",
                text: ""
              });
              this.allproducts.splice(index, 1);
              //console.log("Report - ", myresponse);
              this.loader.hide();
            })
            .catch(() => {
              // const validationerror = JSON.parse(
              //   JSON.stringify(error.response.data)
              // );
              this.loader.hide();
              this.$swal({
                icon: "error",
                title: "Error deleting Product",
                text: ""
              });
            });
        }
      });
    },
    displayLoader() {
      this.loader = this.$loading.show({
        // Optional parameters
        container: null,
        canCancel: false,
        onCancel: this.onCancel,
        color: "#4e73df",
        loader: "bars",
        width: 100,
        height: 100,
        backgroundColor: "#e1e1e1",
        opacity: 0.6,
        zIndex: 999
      });
    },
    fetchAllProducts() {
      this.displayLoader();
      axios
        .get(
          process.env.VUE_APP_ROOT_API + "admin/allProducts?token=" + this.token
        )
        .then(response => {
          const myresponse = JSON.parse(JSON.stringify(response.data.response));
          this.allproducts = myresponse;
          console.log("Report - ", myresponse);
          this.loader.hide();
        })
        .catch(() => {
          this.loader.hide();
          this.$swal({
            icon: "error",
            title: "Session TimeOut",
            text: ""
          });
        });
    },
    getImage(img) {
      return process.env.VUE_APP_ROOT_API + "fetchImage/" + img;
    },
    fetchAllCategories() {
      //this.displayLoader();
      axios
        .get(
          process.env.VUE_APP_ROOT_API +
            "admin/allCategories?token=" +
            this.token
        )
        .then(response => {
          const myresponse = JSON.parse(JSON.stringify(response.data.response));
          this.categories = myresponse;
          // console.log("Report - ", myresponse);
          //this.loader.hide();
        })
        .catch(() => {
          // const validationerror = JSON.parse(
          //   JSON.stringify(error.response.data)
          // );
          // this.loader.hide();
          this.$swal({
            icon: "error",
            title: "Session TimeOut",
            text: ""
          });
        });
    }
  },
  mounted() {
    this.fetchAllCategories();
    this.fetchAllProducts();
  }
};
</script>

<style>
   .vm--modal{
       overflow: auto !important;
   }
</style>