</<template>
    <div class="container-fluid">
    
    
    
    
    
    
    
        <!-- Content Row -->
    
    
    
        <div class="row">
    
    
    
            <!-- Area Chart -->
    
            <div class="col-xl-6 col-lg-6">
    
                <div class="card shadow mb-4">
    
                    <!-- Card Header - Dropdown -->
    
                    <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
    
                        <h6 class="m-0 font-weight-bold text-primary">Add Product</h6>
    
                        <div class="dropdown no-arrow">
    
                            <a class="dropdown-toggle" href="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
    
                          <i class="fas fa-ellipsis-v fa-sm fa-fw text-gray-400"></i>
    
                        </a>
    
                            <div class="dropdown-menu dropdown-menu-right shadow animated--fade-in" aria-labelledby="dropdownMenuLink">
    
                                <div class="dropdown-header">Dropdown Header:</div>
    
                                <a class="dropdown-item" href="#">Action</a>
    
                                <a class="dropdown-item" href="#">Another action</a>
    
                                <div class="dropdown-divider"></div>
    
                                <a class="dropdown-item" href="#">Something else here</a>
    
                            </div>
    
                        </div>
    
                    </div>
    
                    <!-- Card Body -->
    
                    <div class="card-body">
    
                        <div>
    
                            <form @submit.prevent="submitProduct" class="form-group">

                                <div class="form-group">
                                      <select class="form-control col-12" v-model="category" required>
                                        <option value="" selected disabled>Select Categories</option>                  
                                        <option v-for="(info,$index) in categories" :key="$index" :value="info.id">{{ info.category_name }}</option>
                                      </select>
                                </div>

                                <div class="form-group">
                                    <label for="select-type">Product Name</label>
                                    <input v-model="product_name" placeholder="Enter Product Name." class="form-control" type="text" required /> 
                                </div>

                                <div class="form-group">
                                    <label for="product_details">Product Details</label>
                                    <textarea v-model="product_details" placeholder="Enter Product Details" class="form-control" type="text" required>
                                    </textarea>
                                </div>

                                <div class="form-group">
                                    <label for="select-type">Product Price</label>
                                    <input v-model="product_price" placeholder="Enter Product Price." class="form-control" type="number" required /> 
                                </div>

                                <div class="form-group">
                                    <label for="select-type">In Stock</label>
                                    <input v-model="instock" placeholder="How many products are in stock." class="form-control" type="number" required /> 
                                </div>

                                <div class="form-group">
                                    <label for="select-type">Product Discount (Optional)</label>
                                    <input v-model="discount" placeholder="Add discount if available" class="form-control" type="number" required /> 
                                </div>

                                <div class="custom-file form-group" style="margin-bottom: 5px;">
                                    <label for="category_image" style="overflow: hidden;" class="custom-file-label">{{ filename }}</label>
                                    <input @change="onImageChange"  class="custom-file-input" type="file" required /> 
                                </div>

                                <img style="margin-bottom: 5px;" class="img-fluid" v-if="imgUrl" :src="imgUrl" />

                                <div class="form-group">
                                    <button type="submit" class="form-control btn btn-primary">
                                         Save Product
                                    </button>
                                </div>

                            </form>
    
                        </div>
    
                    </div>
    
                </div>
    
            </div>
    
    
    
            <!-- Pie Chart -->
    
            <div class="col-xl-6 col-lg-">
    
                <div class="card shadow mb-4">
    
                    <!-- Card Header - Dropdown -->
    
                    <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
    
                        <h6 class="m-0 font-weight-bold text-primary">All Products</h6>
    
                        <div class="dropdown no-arrow">
    
                            <a class="dropdown-toggle" href="#" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
    
                          <i class="fas fa-ellipsis-v fa-sm fa-fw text-gray-400"></i>
    
                        </a>
    
                            <div class="dropdown-menu dropdown-menu-right shadow animated--fade-in" aria-labelledby="dropdownMenuLink">
    
                                <div class="dropdown-header">Dropdown Header:</div>
    
                                <a class="dropdown-item" href="#">Action</a>
    
                                <a class="dropdown-item" href="#">Another action</a>
    
                                <div class="dropdown-divider"></div>
    
                                <a class="dropdown-item" href="#">Something else here</a>
    
                            </div>
    
                        </div>
    
                    </div>
    
                    <!-- Card Body -->
    
                    <div class="card-body" >
                      <table class="table table-striped table-responsive-md btn-table d-block scroll-wrapper" style="overflow:auto;height: 450px">

                            <thead>
                              <tr>
                                <th>#</th>
                                <th>Picture</th>
                                <th>Name</th>
                                <th>Price</th>
                                <th>Upload Date</th>
                              </tr>
                            </thead>

                            <tbody>
                              <tr v-for="(info,$index) in allproducts" :key="$index" :value="info.id">
                                <th scope="row">{{ $index + 1}}</th>
                                <td>
                                  <img :src="getImage(info.productimages[0].image_location)" class="img-fluid"/>
                                </td>
                                <td>
                                  {{ info.product_name }}
                                </td>
                                <td>{{ info.product_price }}</td>
                                <td>{{ info.created_at }}</td>
                              </tr>

                            </tbody>

                            </table>
                            <router-link to="viewProducts">View All...</router-link>
                        </div>  
                    </div>
    
                </div>
    
            </div>
    
        </div>
    </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      token: localStorage.getItem("admintoken"),
      user: "",
      product_name: "",
      product_details: "",
      product_price: "",
      discount: 0,
      instock: 0,
      product_image: "",
      category: "",
      filename: "Product Image",
      loader: null,
      imgUrl: null,
      myresponse: {},
      categories: [],
      allproducts:[],
      categoryStyles: ["bg-warning", "bg-info", "bg-success", "bg-danger"]
    };
  },
  methods: {
    onImageChange(e) {
      this.product_image = e.target.files[0];
      this.filename = this.product_image.name;
      this.imgUrl = URL.createObjectURL(this.product_image);
    },

    submitProduct(e) {
      this.displayLoader();
      let formData = new FormData();
      formData.append(
        "product_image",
        this.product_image,
        this.product_image.name
      );
      formData.append("product_name", this.product_name);
      formData.append("product_details", this.product_details);
      formData.append("product_price", this.product_price);
      formData.append("discount", this.discount);
      formData.append("in_stock", this.instock);
      formData.append("categories_id", this.category);
      axios
        .post(
          process.env.VUE_APP_ROOT_API +
            "admin/uploadProduct?token=" +
            this.token,
          formData
        )
        .then((res) => {
          //console.log(response.data);
          e.target.reset();
          this.$swal({
            icon: "success",
            title: "Uploaded Successfully",
            text: "Product is now available."
          });

          this.allproducts = res.data.response;          
          this.loader.hide();
        })
        .catch(error => {
          const validationerror = JSON.parse(
            JSON.stringify(error.response.data)
          );
          this.loader.hide();
          //console.log("Error - ",validationerror);
          if (error.response.status == 422) {
            
            const category_name_error = validationerror.errors.category_name;

            if (category_name_error != null) {
              this.$swal({
                icon: "error",
                title: "Fail to upload",
                text: category_name_error
              });
            }
          }

          if (validationerror.errors.product_name != null) {
            //console.log(validationerror);
           
              this.$swal({
                icon: "error",
                title: "Fail to upload",
                text: validationerror.errors.product_name
              });
            
          }
        });
    },
    displayLoader() {
      this.loader = this.$loading.show({
        // Optional parameters
        container: null,
        canCancel: false,
        onCancel: this.onCancel,
        color: "#4e73df",
        loader: "bars",
        width: 100,
        height: 100,
        backgroundColor: "#e1e1e1",
        opacity: 0.6,
        zIndex: 999
      });
    },
    fetchAllCategories() {
      this.displayLoader();
      axios
        .get(
          process.env.VUE_APP_ROOT_API +
            "admin/allCategories?token=" +
            this.token
        )
        .then(response => {
          const myresponse = JSON.parse(JSON.stringify(response.data.response));
          this.categories = myresponse;
          this.loader.hide();
        })
        .catch(() => {
          this.loader.hide();
          this.$swal({
            icon: "error",
            title: "Session TimeOut",
            text: ""
          });
        });
    },
    fetchAllProducts() {
      //this.displayLoader();
      axios
        .get(
          process.env.VUE_APP_ROOT_API +
            "admin/allProducts?token=" +
            this.token
        )
        .then(response => {
          const myresponse = JSON.parse(JSON.stringify(response.data.response));
          this.allproducts = myresponse;
        })
        .catch(() => {
         // this.loader.hide();
          this.$swal({
            icon: "error",
            title: "Session TimeOut",
            text: ""
          });
        });
    },
    getImage(img){
        return process.env.VUE_APP_ROOT_API + "fetchImage/"+img;
    }
  },
  mounted() {
    this.fetchAllCategories();
    this.fetchAllProducts();
  }
};
</script>

