<template>
  <div class="container-fluid">
    <!-- Content Row -->

    <div class="row">
      <!-- Pie Chart -->

      <div class="col-xl-12 col-lg-12">
        <div class="card shadow mb-4">
          <!-- Card Header - Dropdown -->

          <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
            <h6 class="m-0 font-weight-bold text-primary">All Category</h6>

            <div class="dropdown no-arrow">
              <a
                class="dropdown-toggle"
                href="#"
                role="button"
                id="dropdownMenuLink"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                <i class="fas fa-ellipsis-v fa-sm fa-fw text-gray-400"></i>
              </a>

              <div
                class="dropdown-menu dropdown-menu-right shadow animated--fade-in"
                aria-labelledby="dropdownMenuLink"
              >
                <div class="dropdown-header">Dropdown Header:</div>

                <a class="dropdown-item" href="#">Action</a>

                <a class="dropdown-item" href="#">Another action</a>

                <div class="dropdown-divider"></div>

                <a class="dropdown-item" href="#">Something else here</a>
              </div>
            </div>
          </div>

          <!-- Card Body -->

          <div class="card-body">
            <table class="table table-striped table-responsive-md btn-table">
              <thead>
                <tr>
                  <th>#</th>
                  <th>Picture</th>
                  <th>Name</th>
                  <th>Details</th>
                  <th>Upload Date</th>
                  <th>Action</th>
                </tr>
              </thead>

              <tbody>
                <tr v-for="(info,$index) in categories" :key="$index" :value="info.id">
                  <th scope="row">{{ $index + 1 }}</th>
                  <td>
                    <img
                      :src="getImage(info.category_image)"
                      style="max-height:50px;"
                      class="img-fluid"
                    />
                  </td>
                  <td>{{ info.category_name }}</td>
                  <td>{{ info.category_details }}</td>
                  <td>{{ info.created_at }}</td>
                  <td>
                    <button class="btn btn-danger" @click="deleteCategory(info.id,$index)">Delete</button>
                    <button class="btn btn-primary" @click="showModal(info,$index)">Edit</button>
                  </td>
                </tr>
              </tbody>
            </table>
            <a href>View All...</a>
          </div>
        </div>
      </div>
    </div>
    <modal
      v-if="modalData != null"
      name="EditModal"
      :adaptive="true"
      height="auto"
      :scrollable="true"
    >
      <div class="card">
        <div slot="top-right">
          <button class="btn" @click="closeModal"></button>
        </div>
        <div class="card-title container">
          <h3>{{ modalData.category_name }}</h3>
        </div>
        <div class="card-body">
          <div class="row">
            <div class="col-12 col-xl-6 card-title">
              <img
                style="margin-bottom: 5px;"
                class="img-fluid"
                v-if="editDetails.imgUrl"
                :src="editDetails.imgUrl"
              />
              <img
                v-if="editDetails.imgUrl == ''"
                :src="getImage(modalData.category_image)"
                class="img-fluid"
              />
            </div>

            <div class="col-12 col-xl-6" style="margin-bottom:10px;">
              <form @submit.prevent="editCategory" calss="form-group">
                <div class="form-group">
                  <label for="select-type">Category Name</label>
                  <input
                    v-model="editDetails.category_name"
                    placeholder="Category Name."
                    class="form-control"
                    type="text"
                    required
                  />
                </div>

                <div class="form-group">
                  <label for="category_details">Category Details</label>
                  <textarea
                    v-model="editDetails.category_details"
                    placeholder="Category Details"
                    class="form-control"
                    type="text"
                    required
                  ></textarea>
                </div>
                <div><p>*This is optional since this category already has an image.</p></div>
                <div class="custom-file form-group" style="margin-bottom: 5px;">
                  <label for="category_image" style="overflow: hidden;" class="custom-file-label">{{ editDetails.filename }}</label>
                  <input @change="onImageChange" class="custom-file-input" type="file" />
                </div>

                <div class="form-group">
                  <button type="submit" class="form-control btn btn-primary">Edit Category</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      token: localStorage.getItem("admintoken"),
      user: "",
      loader: null,
      imgUrl: null,
      categories: [],
      modalData: null,
      editDetails: {
        currentIndex : "",
        category_id: "",
        category_name: "",
        category_details: "",
        category_image: "",
        filename: "",
        imgUrl: ""
      }
    };
  },
  methods: {
    onImageChange(e) {
      this.editDetails.category_image = e.target.files[0];
      this.editDetails.filename = this.editDetails.category_image.name;
      this.editDetails.imgUrl = URL.createObjectURL(
      this.editDetails.category_image
      );
    },
    editCategory() {
      this.displayLoader();
      let formData = new FormData();
      if (this.editDetails.category_image != "") {
        formData.append(
          "category_image",
          this.editDetails.category_image,
          this.editDetails.category_image.name
        );
      }

      formData.append("category_id", this.editDetails.category_id);
      formData.append("category_name", this.editDetails.category_name);
      formData.append("category_details", this.editDetails.category_details);
      axios
        .post(
          process.env.VUE_APP_ROOT_API +
            "admin/editCategory?token=" +
            this.token,
          formData
        )
        .then(res => {
          this.categories.splice(this.editDetails.currentIndex,1,res.data.response);
          this.$swal({
            icon: "success",
            title: "Edited Successfully",
            text: ""
          });

          this.loader.hide();
        })
        .catch(error => {
          const validationerror = JSON.parse(
            JSON.stringify(error.response.data)
          );
          this.loader.hide();
          if (error.response.status == 422) {
            //console.log(validationerror);
            const category_name_error = validationerror.errors.category_name;
            //const product_name_error = validationerror.errors.product_name;

            if (category_name_error != null) {
              this.$swal({
                icon: "error",
                title: "Use a unique category name",
                text: category_name_error
              });
            }
          }
           if (error.response.data.error != null) {
              this.$swal({
                icon: "error",
                title: "Update Failed",
                text: error.response.data.error
              });
            }
        });
    },
    showModal(info,index) {
      this.editDetails = {
        currentIndex : index,
        category_id: info.id,
        category_name: info.category_name,
        category_details: info.category_details,
        category_image: "",
        filename: "",
        imgUrl: ""
      };
      this.modalData = info;
      this.$modal.show("EditModal");
    },
    closeModal() {
      this.$modal.hide("EditModal");
    },
    deleteCategory(categoryId, index) {
      this.$swal({
        icon: "warning",
        title: "Are you sure?",
        text:
          "You won't be able to revert this, this will also delete all products previously added to this category.",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, Delete it!"
      }).then(result => {
        if (result.value) {
          this.displayLoader();
          axios
            .post(
              process.env.VUE_APP_ROOT_API +
                "admin/deleteCategory?token=" +
                this.token,
              { categoryId: categoryId }
            )
            .then(() => {
              this.$swal({
                icon: "success",
                title: "Category Deleted Successfully",
                text: ""
              });
              this.categories.splice(index, 1);
              this.loader.hide();
            })
            .catch(() => {
              this.loader.hide();
              this.$swal({
                icon: "error",
                title: "Error deleting Category",
                text: ""
              });
            });
        }
      });
    },
    displayLoader() {
      this.loader = this.$loading.show({
        // Optional parameters
        container: null,
        canCancel: false,
        onCancel: this.onCancel,
        color: "#4e73df",
        loader: "bars",
        width: 100,
        height: 100,
        backgroundColor: "#e1e1e1",
        opacity: 0.6,
        zIndex: 999
      });
    },
    getImage(img) {
      return process.env.VUE_APP_ROOT_API + "fetchCategoryImage/" + img;
    },
    fetchAllCategories() {
      this.displayLoader();
      axios
        .get(
          process.env.VUE_APP_ROOT_API +
            "admin/allCategories?token=" +
            this.token
        )
        .then(response => {
          const myresponse = JSON.parse(JSON.stringify(response.data.response));
          this.categories = myresponse;
          this.loader.hide();
        })
        .catch(() => {
          this.loader.hide();
          this.$swal({
            icon: "error",
            title: "Session TimeOut",
            text: ""
          });
        });
    }
  },
  mounted() {
    this.fetchAllCategories();
  }
};
</script>