<template>
  <div class="container-fluid">
    <!-- Content Row -->

    <div class="row">
      <!-- Pie Chart -->

      <div class="col-xl-12 col-lg-12">
        <div class="card shadow mb-4">
          <!-- Card Header - Dropdown -->

          <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
            <h6 class="m-0 font-weight-bold text-primary">All Users</h6>

            <div class="dropdown no-arrow">
              <a
                class="dropdown-toggle"
                href="#"
                role="button"
                id="dropdownMenuLink"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                <i class="fas fa-ellipsis-v fa-sm fa-fw text-gray-400"></i>
              </a>

              <div
                class="dropdown-menu dropdown-menu-right shadow animated--fade-in"
                aria-labelledby="dropdownMenuLink"
              >
                <div class="dropdown-header">Dropdown Header:</div>

                <a class="dropdown-item" href="#">Action</a>

                <a class="dropdown-item" href="#">Another action</a>

                <div class="dropdown-divider"></div>

                <a class="dropdown-item" href="#">Something else here</a>
              </div>
            </div>
          </div>

          <!-- Card Body -->

          <div class="card-body">
            <table class="table table-striped table-responsive-md btn-table">
              <thead>
                <tr>
                  <th>#</th>
                  <th>Firstname</th>
                  <th>Lastname</th>
                  <th>Location</th>
                  <th>Status</th>
                  <th>Registration Date</th>
                  <th>Action</th>
                </tr>
              </thead>

              <tbody>
                <tr v-for="(info,$index) in allUsers" :key="$index" :value="info.id">
                  <th scope="row">{{ $index + 1 }}</th>
                  <td>{{ info.firstname }}</td>
                  <td>{{ info.lastname }}</td>
                  <td>{{ info.country +", "+info.state +", "+info.city }}</td>
                  <td>
                    {{ info.status == 0 ? "Not Verified" :
                    info.status == 1 ? "Verified" :
                    info.status == 2 ? "Disabled" : "Unknown Status" }}
                  </td>
                  <td>{{ info.created_at }}</td>
                  <td>
                    <button
                      class="btn btn-sm"
                       :class="{ 'btn-danger' : info.status <= 1, 'btn-success' : info.status == 2}"
                      @click="updateUser(info.status,$index, info.id)"
                    >
                      <i
                        class="fa"
                        :class="{ 'fa-ban' : info.status <= 1, 'fa-check' : info.status == 2}"
                      ></i>
                    </button>
                    <button class="btn btn-sm btn-primary" @click="showModal(info)">
                      <i class="fa fa-eye"></i>
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
            <a href>View All...</a>
          </div>
        </div>
      </div>
    </div>
    <modal
      v-if="modalData != null"
      name="ViewModal"
      :adaptive="true"
      :scrollable="true"
      height="auto"
      style="over-flow:auto !important"
    >
      <div class="card">
        <div slot="top-right">
          <button class="btn" @click="closeModal"></button>
        </div>

        <div class="card-body">
          <div class="card-title">
              <h3>{{ modalData.firstname + " "+ modalData.lastname }}</h3>
          </div>
          <div class="row">
              <div class="col-3">Status</div>
              <div class="col-9"> {{ modalData.status == 0 ? "Not Verified" :
                    modalData.status == 1 ? "Verified" :
                    modalData.status == 2 ? "Disabled" : "Unknown Status" }}</div>
          </div>

          <div class="row">
              <div class="col-3">Email</div>
              <div class="col-9">{{ modalData.email }}</div>
          </div>

          <div class="row">
              <div class="col-3">Address</div>
              <div class="col-9">{{ modalData.shipping_address }}</div>
          </div>

          <div class="row">
              <div class="col-3">Phone</div>
              <div class="col-9">{{ modalData.phone }}</div>
          </div>

          <div class="row">
              <div class="col-3">Country</div>
              <div class="col-9">{{ modalData.country }}</div>
          </div>

          <div class="row">
              <div class="col-3">State</div>
              <div class="col-9">{{ modalData.state }}</div>
          </div>

          <div class="row">
              <div class="col-3">Street</div>
              <div class="col-9">{{ modalData.street }}</div>
          </div>

          <div class="row">
              <div class="col-3">Reg Date</div>
              <div class="col-9">{{ modalData.created_at }}</div>
          </div>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      token: localStorage.getItem("admintoken"),
      allUsers: [],
      modalData: {}
    };
  },
  methods: {
    showModal(info) {
      this.modalData = info;
      this.$modal.show("ViewModal");
    },
    closeModal() {
      this.$modal.hide("ViewModal");
    },
    updateUser(status, index, userId) {
      var updateType = status == 1 ? "Block" : status == 2 ? "Unblock" : "";
      this.$swal({
        icon: "warning",
        title: "Are you sure?",
        text: "You're about to " + updateType + " a user, ",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, Do it!",
      }).then((result) => {
        if (result.value) {
          this.displayLoader();
          this.loadUser = false;
          status = status == 1 ? 2 : status == 2 ? 1 : 2;
          axios
            .post(
              process.env.VUE_APP_ROOT_API +
                "admin/processUser?token=" +
                this.token +
                "&id=" +
                userId +
                "&status=" +
                status
            )
            .then(() => {
              this.allUsers[index].status = status;
              this.loader.hide();
              this.$swal({
                icon: "success",
                title: "User status has been updated",
                text: "",
              });
            })
            .catch(() => {
              this.loader.hide();
              this.$swal({
                icon: "error",
                title: "Unknown Error Occured",
                text: "",
              });
            });
        }
      });
    },
    fetchAllUsers() {
      this.displayLoader();
      axios
        .get(
          process.env.VUE_APP_ROOT_API +
            "admin/fetchAllUsers?token=" +
            this.token
        )
        .then((response) => {
          const myresponse = JSON.parse(JSON.stringify(response.data));
          this.allUsers = myresponse;
          //console.log("Report - ", myresponse[0].status);
          this.loader.hide();
        })
        .catch(() => {
          this.loader.hide();
          this.$swal({
            icon: "error",
            title: "Session TimeOut",
            text: "",
          });
        });
    },
    displayLoader() {
      this.loader = this.$loading.show({
        // Optional parameters
        container: null,
        canCancel: false,
        onCancel: this.onCancel,
        color: "#4e73df",
        loader: "bars",
        width: 100,
        height: 100,
        backgroundColor: "#e1e1e1",
        opacity: 0.6,
        zIndex: 999,
      });
    },
  },
  mounted() {
    // this.fetchAllCategories();
    this.fetchAllUsers();
  },
};
</script>

<style>
.vm--modal {
  overflow: auto !important;
}
</style>