<template>
  <div class="container-fluid">
    <!-- Content Row -->

    <div class="row">
      <!-- Pie Chart -->

      <div class="col-xl-12 col-lg-12">
        <div class="card shadow mb-4">
          <!-- Card Header - Dropdown -->

          <div class="card-header py-3 d-flex flex-row align-items-center justify-content-between">
            <h6 class="m-0 font-weight-bold text-primary">States for {{ query_country_name }}</h6>

            <div class="dropdown no-arrow">
              <a
                class="dropdown-toggle"
                href="#"
                role="button"
                id="dropdownMenuLink"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                <i class="fas fa-ellipsis-v fa-sm fa-fw text-gray-400"></i>
              </a>

              <div
                class="dropdown-menu dropdown-menu-right shadow animated--fade-in"
                aria-labelledby="dropdownMenuLink"
              >
                <div class="dropdown-header">Dropdown Header:</div>

                <a class="dropdown-item" href="#">Action</a>

                <a class="dropdown-item" href="#">Another action</a>

                <div class="dropdown-divider"></div>

                <a class="dropdown-item" href="#">Something else here</a>
              </div>
            </div>
          </div>

          <!-- Card Body -->

          <div class="card-body">
            <table class="table table-striped table-responsive-md btn-table">
              <thead>
                <tr>
                  <th>#</th>
                  <th>State Name</th>
                  <th>Price</th>
                  <th>Action</th>
                </tr>
              </thead>

              <tbody>
                <tr v-for="(info,$index) in shippingstates" :key="$index" :value="info.id">
                  <th scope="row">{{ $index + 1 }}</th>
                  <td>{{ info.state_name }}</td>
                  <td>{{ info.state_price }}</td>
                  <td>
                    <button class="btn btn-danger" @click="deleteShippingState(info.id,$index)">Delete</button>
                    <button class="btn btn-primary" @click="showModal(info)">Edit</button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <modal
    v-if="modalData != null"
      name="EditModal"
      :adaptive="true"
      :scrollable="true"
      height="auto"
      style="over-flow:auto !important"
    >
      <div class="card">
        <div slot="top-right">
          <button class="btn" @click="closeModal"></button>
        </div>
        <!-- <div class="card-title container">
             <h3>{{ modalData.product_name }}</h3>
        </div>-->
        <div class="card-body">
          <div class="row">
            <!-- <div class="col-12"> -->
            <div class="col-12 col-xl-12" style="margin-bottom:10px;">
              <form @submit.prevent="editShippingState()">
                <div class="form-group">
                  <label for="select-type">Shipping State</label>
                  <input
                    v-model="editDetails.state_name"
                    placeholder="Enter State Name."
                    class="form-control"
                    type="text"
                    required
                  />
                </div>

                <div class="form-group">
                  <label for="product_details">State Shipping Price</label>
                  <input
                    v-model="editDetails.state_price"
                    placeholder="Enter State Price."
                    class="form-control"
                    type="number"
                    oninput="validity.valid||(value='');"
                    min="1"
                    required
                  />
                </div>
                <div class="form-group">
                  <button type="submit" class="form-control btn btn-primary">Edit State</button>
                </div>
              </form>
            </div>
            <!-- </div> -->
          </div>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      token: localStorage.getItem("admintoken"),
      user: "",
      loader: null,
      categories: [],
      myresponse: {},
      shippingstates: [],
      modalData: null,
      query_country_name: "",
      editDetails: {
        state_id: "",
        state_name: "",
        state_price: "",
      },
      states : 0,
      query_country_id: 0,
    };
  },
  methods: {
    editShippingState() {
      this.displayLoader();
      axios
        .post(
          process.env.VUE_APP_ROOT_API +
            "admin/editShippingState?token=" +
            this.token,
          this.editDetails
        )
        .then((res) => {
          //console.log(response.data);
          //e.target.reset();
          this.$swal({
            icon: "success",
            title: "Edited Successfully",
            text: "State is now available.",
          });

          this.shippingstates = res.data.response;

          this.loader.hide();
        })
        .catch((error) => {
          const validationerror = JSON.parse(
            JSON.stringify(error.response.data)
          );
          this.loader.hide();
          if (error.response.status == 422) {
            //console.log(validationerror);
            const state_name_error = validationerror.errors.state_name;
            if (state_name_error != null) {
              this.$swal({
                icon: "error",
                title: "Use a unique state name",
                text: state_name_error,
              });
            }
            // if (category_name_error != null) {
            //   this.$swal({
            //     icon: "error",
            //     title: "Fail to Edit",
            //     text: category_name_error
            //   });
            // }
          }
        });
    },
    showModal(info) {
      this.editDetails = {
        state_id: info.id,
        state_name: info.state_name,
        state_price: info.state_price,
      };
      this.modalData = info;
      this.$modal.show("EditModal");
    },
    closeModal() {
      this.$modal.hide("EditModal");
    },
    deleteShippingState(state_id, index) {
      //alert(productId);
      this.$swal({
        icon: "warning",
        title: "Are you sure?",
        text: "You won't be able to revert this.",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, Delete it!",
      }).then((result) => {
        if (result.value) {
          this.displayLoader();
          axios
            .post(
              process.env.VUE_APP_ROOT_API +
                "admin/deleteShippingState?token=" +
                this.token,
              { state_id: state_id }
            )
            .then(() => {
              //const myresponse = JSON.parse(JSON.stringify(response.data.response));
              // this.categories = myresponse;
              this.$swal({
                icon: "success",
                title: "State Deleted Successfully",
                text: "",
              });
              this.shippingstates.splice(index, 1);
              //console.log("Report - ", myresponse);
              this.loader.hide();
            })
            .catch(() => {
              // const validationerror = JSON.parse(
              //   JSON.stringify(error.response.data)
              // );
              this.loader.hide();
              this.$swal({
                icon: "error",
                title: "Error deleting Country",
                text: "",
              });
            });
        }
      });
    },
    displayLoader() {
      this.loader = this.$loading.show({
        // Optional parameters
        container: null,
        canCancel: false,
        onCancel: this.onCancel,
        color: "#4e73df",
        loader: "bars",
        width: 100,
        height: 100,
        backgroundColor: "#e1e1e1",
        opacity: 0.6,
        zIndex: 999,
      });
    },
    fetchCountryStates() {
      this.displayLoader();
      axios
        .get(
          process.env.VUE_APP_ROOT_API +
            "admin/fetchCountryStates?token=" +
            this.token+'&country_id='+this.query_country_id
        )
        .then((response) => {
          const myresponse = JSON.parse(JSON.stringify(response.data.response));
          this.shippingstates = myresponse;
          this.loader.hide();
        })
        .catch(() => {
          this.loader.hide();
          this.$swal({
            icon: "error",
            title: "Session TimeOut",
            text: "",
          });
        });
    },
  },
  mounted() {
    
    this.query_country_id = this.$route.params.country_id;
    this.query_country_name = this.$route.params.country_name;
    
    this.fetchCountryStates(this.query_product_id);
    this.fetchAllShippingCountry();
  },
};
</script>

<style>
.vm--modal {
  overflow: auto !important;
}
</style>